#!/usr/bin/env node
/**
 * /zest:logout command handler
 *
 * Signs out of Zest and removes stored credentials
 */

import { logout } from "../auth/authentication.js";
import { loadSession } from "../auth/session-manager.js";
import { logger } from "../utils/logger.js";

async function main() {
  try {
    // Check if authenticated
    const session = await loadSession();
    if (!session) {
      console.log("‚ÑπÔ∏è  Not authenticated");
      return;
    }

    const email = session.email;

    // Logout
    await logout();

    console.log(`‚úÖ Signed out (${email})`);
  } catch (error) {
    logger.error("Logout failed", error);
    console.error("‚ùå Logout failed");
    if (error instanceof Error) {
      console.error(`   ${error.message}`);
    }
    process.exit(1);
  }
}

main();
