/**
 * Authentication flow implementation
 *
 * Handles device code flow for Zest authentication (like GitHub CLI)
 * No local server needed - works everywhere including WSL, remote servers, etc.
 */

import open from "open";
import { CLIENT_ID, WEB_APP_URL } from "../config/constants.js";
import { fetchUserWorkspaces } from "../supabase/workspace-fetcher.js";
import { logger } from "../utils/logger.js";
import { type AuthSession, clearSession, loadSession, saveSession } from "./session-manager.js";

interface DeviceCodeResponse {
  authCode: string; // Secure polling token
  deviceCode: string; // User-friendly code (ABCD-1234)
  verificationUrl: string;
  expiresIn: number;
  interval: number;
}

interface DevicePollResponse {
  status: "pending" | "authorized";
  session?: {
    access_token: string;
    refresh_token: string;
    expires_at?: number; // Unix timestamp (seconds) - access token expiration
    expires_in?: number; // Seconds until access token expiration
    refresh_token_expires_at?: number; // Unix timestamp (seconds) - refresh token expiration
    refresh_token_expires_in?: number; // Seconds until refresh token expiration
    user: {
      id: string;
      email?: string;
      user_metadata: Record<string, unknown>;
    };
  };
}

/**
 * Start the device code authentication flow
 */
export async function startAuthFlow(): Promise<AuthSession> {
  try {
    logger.info("Starting device code authentication flow");

    // Step 1: Initiate device code flow
    const deviceCodeData = await initiateDeviceCode();

    // Step 2: Display code to user and open browser
    displayAuthInstructions(deviceCodeData);

    // Step 3: Poll for authorization
    const session = await pollForAuthorization(deviceCodeData);

    // Step 4: Save session
    await saveSession(session);
    logger.info("Device code authentication successful");

    // Step 5: Select default workspace
    await selectDefaultWorkspace();

    return session;
  } catch (error) {
    logger.error("Device code authentication failed", error);
    throw error;
  }
}

/**
 * Initiate device code flow - get device code and user code
 */
async function initiateDeviceCode(): Promise<DeviceCodeResponse> {
  try {
    const response = await fetch(`${WEB_APP_URL}/api/auth/device/initiate`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        clientId: CLIENT_ID,
      }),
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Failed to initiate device code: ${response.status} - ${errorText}`);
    }

    const data = (await response.json()) as DeviceCodeResponse;
    logger.debug("Device code initiated successfully", { deviceCode: data.deviceCode });

    return data;
  } catch (error) {
    logger.error("Failed to initiate device code", error);
    throw error;
  }
}

/**
 * Display authentication instructions to the user
 */
function displayAuthInstructions(deviceCodeData: DeviceCodeResponse): void {
  // Minimal output to avoid Claude CLI truncation
  console.log(`\nüîê Enter code in browser: ${deviceCodeData.deviceCode}`);
  console.log(`üì± URL: ${deviceCodeData.verificationUrl}\n`);

  // Try to open browser automatically
  try {
    open(deviceCodeData.verificationUrl);
  } catch (error) {
    logger.warn("Failed to open browser automatically", error);
  }
}

/**
 * Poll the API to check if user has authorized the device
 */
async function pollForAuthorization(deviceCodeData: DeviceCodeResponse): Promise<AuthSession> {
  const maxAttempts = Math.floor(deviceCodeData.expiresIn / deviceCodeData.interval);
  let attempts = 0;

  // Show waiting message once
  console.log("‚è≥ Waiting for authorization...\n");

  while (attempts < maxAttempts) {
    attempts++;

    try {
      const response = await fetch(`${WEB_APP_URL}/api/auth/device/poll`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          authCode: deviceCodeData.authCode,
        }),
      });

      if (!response.ok) {
        const errorData = (await response.json()) as { error?: string };
        throw new Error(errorData.error || "Polling failed");
      }

      const data = (await response.json()) as DevicePollResponse;

      if (data.status === "authorized" && data.session) {
        console.log("‚úÖ Authorization successful!");
        logger.info("Device authorized successfully");

        const now = Date.now();

        // Calculate access token expiration time from Supabase session data
        let expiresAt: number;
        if (data.session.expires_at) {
          // Supabase returns expires_at in seconds, convert to milliseconds
          expiresAt = data.session.expires_at * 1000;
        } else if (data.session.expires_in) {
          // Fallback: use expires_in (seconds) to calculate expiration
          expiresAt = now + data.session.expires_in * 1000;
        } else {
          // Default: assume 1 hour expiration (Supabase default)
          expiresAt = now + 3600 * 1000;
          logger.warn("No access token expiration info from Supabase, assuming 1 hour");
        }

        // Calculate refresh token expiration time (if provided - many configs have refresh tokens that never expire)
        let refreshTokenExpiresAt: number | undefined;
        if (data.session.refresh_token_expires_at) {
          // Supabase returns expires_at in seconds, convert to milliseconds
          refreshTokenExpiresAt = data.session.refresh_token_expires_at * 1000;
        } else if (data.session.refresh_token_expires_in) {
          // Fallback: use expires_in (seconds)
          refreshTokenExpiresAt = now + data.session.refresh_token_expires_in * 1000;
        }
        // If no expiration info provided, leave as undefined (refresh token never expires)

        logger.debug(`Access token will expire at ${new Date(expiresAt).toISOString()}`);
        if (refreshTokenExpiresAt) {
          logger.debug(
            `Refresh token will expire at ${new Date(refreshTokenExpiresAt).toISOString()}`,
          );
        } else {
          logger.debug("Refresh token does not expire");
        }

        return {
          accessToken: data.session.access_token,
          refreshToken: data.session.refresh_token,
          expiresAt,
          refreshTokenExpiresAt,
          userId: data.session.user.id,
          email: data.session.user.email || "",
          workspaceId: undefined,
        };
      }

      // Still pending - wait silently and try again
      if (data.status === "pending") {
        await new Promise((resolve) => setTimeout(resolve, deviceCodeData.interval * 1000));
        continue;
      }
    } catch (error) {
      // Check if it's an authorization denied error
      if (error instanceof Error && error.message.includes("denied")) {
        throw new Error("Authorization denied by user");
      }

      // Check if it's an expired error
      if (error instanceof Error && error.message.includes("expired")) {
        throw new Error("Device code expired. Please try again.");
      }

      // For network errors, log and retry
      logger.warn("Polling attempt failed", error);
      await new Promise((resolve) => setTimeout(resolve, deviceCodeData.interval * 1000));
      continue;
    }
  }

  throw new Error("Authorization timeout - no response received");
}

/**
 * Select default workspace after authentication
 * Auto-selects the first (earliest created) workspace
 */
async function selectDefaultWorkspace(): Promise<void> {
  try {
    logger.info("Selecting default workspace");

    // Fetch user's workspaces
    const workspaces = await fetchUserWorkspaces();

    if (workspaces.length === 0) {
      // No workspaces - shouldn't happen as device auth page checks
      // But handle gracefully
      console.log("\n‚ö†Ô∏è  No workspace found");
      console.log(`   Create one at: ${WEB_APP_URL}/onboarding\n`);
      return;
    }

    // Sort by created_at ASC (earliest first)
    workspaces.sort((a, b) => new Date(a.created_at).getTime() - new Date(b.created_at).getTime());

    const defaultWorkspace = workspaces[0];

    // Update the session with workspace info
    const session = await loadSession();
    if (session) {
      session.workspaceId = defaultWorkspace.id;
      session.workspaceName = defaultWorkspace.name;
      await saveSession(session);
    }

    // Show workspace info
    if (workspaces.length === 1) {
      console.log(`üè¢ Workspace: ${defaultWorkspace.name}`);
    } else {
      console.log(`üè¢ Workspace: ${defaultWorkspace.name} (default)`);
      console.log(`   ${workspaces.length - 1} other workspace(s) available`);
      console.log("   Run /zest:workspace to switch");
    }

    logger.info("Default workspace selected", {
      workspace_id: defaultWorkspace.id,
      workspace_name: defaultWorkspace.name,
      total_workspaces: workspaces.length,
    });
  } catch (error) {
    logger.error("Failed to select default workspace", error);
    // Don't throw - authentication already succeeded
    console.log("\n‚ö†Ô∏è  Could not load workspaces");
    console.log("   Run /zest:workspace later to select one\n");
  }
}

/**
 * Logout and clear session
 */
export async function logout(): Promise<void> {
  try {
    await clearSession();
    logger.info("Logged out successfully");
  } catch (error) {
    logger.error("Failed to logout", error);
    throw error;
  }
}
